<template>
  <div>
    <div ref="container"></div>
  </div>
</template>
<script>
import { Application, Sprite, Assets } from 'pixi.js';
export default {
  mounted() {
    this.$nextTick(() => this.init())
  },  
  methods: {
    async init() {
      const app = new Application();

      await app.init()

      this.$refs.container.appendChild(app.canvas)

      const texture = await Assets.load(require('@/assets/images/test.jpg'))

      const bunny = new Sprite(texture)

      bunny.x = app.renderer.width / 2
      bunny.y = app.renderer.height / 2

      bunny.anchor.x = 0.5
      bunny.anchor.y = 0.5

      app.stage.addChild(bunny)

      app.ticker.add(() => {
        bunny.rotation += 0.01
      })

    }
  }
}
</script>